import {Component, OnInit} from '@angular/core';
import {UserService} from "../user.service";
import {Router} from "@angular/router";
import * as firebase from 'firebase';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {
  formVisibility = false;
  textToAdd: string;
  buttonsIndex :number;
  showButtons = false;
  todos: any;
  outArr: any = [];
  detailedPost: any;
  showPost :boolean = false;
  detailedIndex: number;
  currList: any;
  newListName: string;
  showButtonsToggle(valueToSet, index) {
    this.showButtons = valueToSet;
    this.buttonsIndex = index;
  }

  constructor(private userSVC: UserService, private router: Router) {

  }

  toggleForm(list) {
    this.formVisibility = !this.formVisibility;
    this.currList = list;
  }

  ngOnInit() {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        firebase.database().ref().child(user.uid).on('value', snap => {
          if (snap.val()) {
            this.todos = snap.val();
            this.outArr = [];
            for(let key in this.todos){
              this.outArr.push({
                key: key,
                listKeys: this.todos[key].list?Object.keys(this.todos[key].list):[],
                list: this.todos[key]
              })
            }
          } else {
            this.todos = [];
            this.outArr = [];
          }

        });
      } else {
        this.router.navigate(['login/']);
      }
    });
  }

  addOne(list, text){
    this.userSVC.addOne(list, text);
    this.textToAdd = '';
    this.formVisibility = !this.formVisibility;

  }

  deleteCard(list, index) {
    this.userSVC.deleteOne(list, index);
  }

  saveCard(list, index, text) {
    this.userSVC.saveOne(list, index, text);
  }

  showDetailed(list, key, cardToShow){
    this.detailedPost = {
      list: list,
      key: key,
      data: cardToShow
    };
    this.showPost = !this.showPost;
    this.detailedIndex = cardToShow;
  }

  closeCard(close){
    this.showPost = close;
  }

  newList(name) {
    this.userSVC.newList(name);
    this.newListName = '';
  }

  deleteList(list){
    if (confirm('Are you sure? You can not revert changes after that.')) {
      this.userSVC.deleteList(list);
    };
  }

}




