import {Injectable} from '@angular/core';
import {  Router} from '@angular/router'
import * as firebase from 'firebase';

@Injectable()
export class UserService {
  userLoggedIn: boolean = false;
  loggedInUser: any;
  authUser: any;

  constructor( private router: Router) {
    firebase.initializeApp({
      apiKey: "AIzaSyBgLXnjFDNECD87UIBFsWX7El9ukGXVTBI",
      authDomain: "myngapp-d75d5.firebaseapp.com",
      databaseURL: "https://myngapp-d75d5.firebaseio.com",
      projectId: "myngapp-d75d5",
      storageBucket: "myngapp-d75d5.appspot.com",
      messagingSenderId: "664339736457"
    })
  }

  register(email: string, password: string) {
    firebase.auth().createUserWithEmailAndPassword(email, password)
      .then((response) => {
        console.dir(response.email);
        alert('Welcome ' + response.email);
        this.initializeList(response.uid);
        this.router.navigate(['/list']);
        })
      .catch(function (error) {
        alert(`${error.message} Please try Again!`)
      })
  }

  getCurr() {
      return firebase.auth().currentUser;
  }

  login(loginEmail: string, loginPaswword: string) {
    firebase.auth().signInWithEmailAndPassword(loginEmail, loginPaswword)
      .then(() => {
        alert('Welcome');
        this.router.navigate(['list']);
      })
      .catch(function (error) {
        alert(`${error.message} Unable to login. Try again!`);
      });
  }


  socProvider: any;

  setProvider(provider) {
    this.socProvider = provider=='Facebook'?new firebase.auth.FacebookAuthProvider():new firebase.auth.GoogleAuthProvider();
    this.loginPopUp(this.socProvider);
  }

  loginPopUp(social) {
    firebase.auth().signInWithPopup(social)
      .then((result) => {
        alert('Welcome ' + result.user.displayName);
        this.router.navigate(['list']);
      })
      .catch(function(error) {
        alert(`${error.message} Unable to login. Try again!`);
      });
  }

  logout() {
    firebase.auth().signOut().then(function () {
      alert(`Logged out`);
    }, function (error) {
        alert(`${error.message} Unable to logout. Try again!`);
      }
    );
  }

  initializeList(userId) {
    firebase.database().ref(userId + '/default/').set({
      title: 'Default',
      list: {

      }
    });
  }

  addOne(list, text){
    firebase.database().ref(firebase.auth().currentUser.uid + '/' + list + '/list/').push().set({
      todo: text
    });

  }

  deleteOne(list, index){
    firebase.database().ref(firebase.auth().currentUser.uid + '/' + list + '/list/' + index).remove();

  }

  saveOne(list, index, text) {
    firebase.database().ref(firebase.auth().currentUser.uid + '/' + list + '/list/' + index).set({
      todo: text
    });
  }

  newList(name) {
    firebase.database().ref(firebase.auth().currentUser.uid + '/' + name + '/').set({
      title: name,
      list: {

      }
    });
  }

  deleteList(list) {
    firebase.database().ref(firebase.auth().currentUser.uid + '/' + list + '/').remove();
  }

}



